export function BubbleSort(Array, setArray, ANIMATION_SPEED) {
  const arr = [];
  var n = Array.length;
  for (let i = 0; i < n; i++) {
    arr.push(Array[i].value);
  }
  var sorted = false;
  const animation_array = [];
  do {
    sorted = true;
    for (let i = 0; i < n - 1; i++) {
      animation_array.push([0, i, i + 1]);
      if (arr[i] > arr[i + 1]) {
        [arr[i], arr[i + 1]] = [arr[i + 1], arr[i]];
        animation_array.push([1, i, i + 1]);
        sorted = false;
      }
      animation_array.push([-1, i, i + 1]);
    }
    n--;
    animation_array.push([2, n]);
  } while (!sorted);
  // console.log(n);
  for (let i = 0; i < Array.length; i++) {
    animation_array.push([2, i]);
  }
  animation_array.push(["stop"]);
  // console.log(arr);
  Animate(animation_array, Array, setArray, ANIMATION_SPEED);
}

export function QuickSort(Array, setArray, ANIMATION_SPEED) {
  const arr = [];
  var n = Array.length;
  for (let i = 0; i < n; i++) {
    arr.push(Array[i].value);
  }
  const animation_array = [];
  QS(arr, 0, arr.length - 1, animation_array);
  for (let i = 0; i < Array.length; i++) {
    animation_array.push([2, i]);
  }
  animation_array.push(["stop"]);
  Animate(animation_array, Array, setArray, ANIMATION_SPEED);
  console.log(arr);
  console.log(animation_array);
}

function QS(array, start, end, animation_array) {
  //console.log(start, end);
  if (start >= end) {
    if (start >= 0 && start <= array.length) {
      animation_array.push([2, start]);
    }
    return;
  }
  let pivot = start;
  let left = start + 1;
  let right = end;
  animation_array.push([3, pivot]);
  while (right >= left) {
    animation_array.push([0, left, right]);
    if (array[right] < array[pivot] && array[left] > array[pivot]) {
      [array[right], array[left]] = [array[left], array[right]];
      animation_array.push([1, left, right]);
    }
    animation_array.push([-1, left, right]);
    if (array[left] <= array[pivot]) {
      left += 1;
    }
    if (array[right] >= array[pivot]) {
      right -= 1;
    }
  }
  animation_array.push([-3, pivot]);
  if (pivot !== right) {
    [array[right], array[pivot]] = [array[pivot], array[right]];
    animation_array.push([1, pivot, right]);
    animation_array.push([2, right]);
  }
  QS(array, start, right - 1, animation_array);
  QS(array, right + 1, end, animation_array);
}

///////////////////////////////////////////

function reconstruct(arr) {
  const array = [];
  const size = arr.length;
  for (let i = 0; i < size; i++) {
    array.push({
      value: arr[i].value,
      state: arr[i].state, // 0
    });
  }
  return array;
}

function Animate(animation_array, Array, setArray, ANIMATION_SPEED) {
  var arr = Array;
  for (let i = 0; i < animation_array.length; i++) {
    setTimeout(() => {
      switch (animation_array[i][0]) {
        case 0: //compare
          arr[animation_array[i][1]].state = 1;
          arr[animation_array[i][2]].state = 1;
          break;
        case -1: //stop compare
          arr[animation_array[i][1]].state = 0;
          arr[animation_array[i][2]].state = 0;
          break;
        case 1: //swap
          let j = animation_array[i][1];
          let k = animation_array[i][2];
          [arr[j], arr[k]] = [arr[k], arr[j]];
          setArray(reconstruct(arr));
          break;
        case 2: //sorted
          arr[animation_array[i][1]].state = 2;
          setArray(reconstruct(arr));
          break;
        case 3: // pivot
          arr[animation_array[i][1]].state = 3;
          setArray(reconstruct(arr));
          break;
        case -3: //stop pivot
          arr[animation_array[i][1]].state = 0;
          setArray(reconstruct(arr));
          break;
        default:
          console.log("done");
      }
    }, ANIMATION_SPEED * i);
  }
}

